

#list-articles{
  margin-top: 7.1875rem;
  margin-bottom: 7.1875rem;
}

.articles__title{
  color: var(--main-color);
}

.articles-wrapper:not(:first-of-type){
  margin-top: 5rem;
}

.article-card{
  background: #FFFFFF;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 1rem;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;  
  margin-top: 1.5rem;
  &:first-of-type{
    margin-top: 1rem;
  }
}

.article__img{
  height: 160px;
  width: 223px;
  background-color: var(--main-color);
  border-radius: 1em 0 0 1em;
  object-fit: cover;
  object-position: center;
}

.article-card__body{
  padding: 1em 0 1rem 1rem;
  width: calc(100% - 12em);
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
}
.article-card__title{
  color: var(--main-color);
  -webkit-line-clamp: 2;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-box-orient: vertical;  
  font-weight: 600;
}
.article-card__text{
  font-family: 'Gotham';
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 21px;
  color: #262626;
  margin-top: 1rem;
  -webkit-line-clamp: 3;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-box-orient: vertical;
}
.article-card__arrow{
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  flex-wrap: nowrap;
  max-width: 10rem;
  min-width: 3rem;
  width: 10rem;
}